import React from 'react';
import { Navigator } from 'react-onsenui';
import './App.css';
import LoginPage from './LoginPage/LoginPage';

class App extends React.Component {
  renderPage(route, navigator) {
    const props = route.props || {};
    props.navigator = navigator;
    return React.createElement(route.component, props);
  }

  render() {
    
    const locations = [
      {
        title: "Tunnels of Moose Jaw",
        subtitle: "Guided tour",
        description: "Head beneath the streets of downtown Moose Jaw and undergo two guided theatrical tours. Relive Al Capone’s bootlegging days in the Chicago Connection tour and experience first-hand the hardships of early Chinese immigrants in the Passage to Fortune tour. We are a family friendly attraction that offers an experience like no other.",
        image: 'https://www.tourismsaskatchewan.com/large/Tunnels_Z3.jpg',
        address: '18 Main St N',
        hours: 'Mon-Sat, 10-19',
        objectID: 0
      },
      {
        title: "Mac the Moose",
        subtitle: "A steel and concrete sculpture of a moose",
        description: "Mac the Moose is a steel and concrete sculpture of a moose in Moose Jaw, Saskatchewan. Formerly the World's Largest Moose, it is claimed to be North America's largest moose as well as the World's Second Largest Moose at 32 feet (9.8 m) feet tall and a weight of approximately 10 long tons (10,000 kg)",
        image: "https://i.pinimg.com/originals/7a/cb/94/7acb940429f3fe4efecb50ad26ff31f3.jpg",
        completed: true,
        address: '450 Diefenbaker Dr',
        hours: 'Mon-Sun, 24/7',
        objectID: 1
      },
      {
        title: "The Casino Moose Jaw",
        subtitle: "First-class gaming",
        description: "Enjoy first-class gaming, relaxed dining and world-class entertainment at one of Western Canada's premier tourist destinations.",
        image: "https://casinoregina.com/pub/f.Poker-room.1.jpg",
        address: '21 Fairford St E',
        hours: 'Mon-Sun, 24/7',
        objectID: 2
      }
    ];
    
    return (
      <Navigator
        initialRoute={{component: LoginPage, props: {locations: locations, key: 'LOGIN_PAGE'}}}
        renderPage={this.renderPage}
      />
    );
  }
}

