import React, { Component } from 'react';
import {Page, List, ListHeader, ListItem, Icon} from 'react-onsenui';
import ActivityPage from '../ActivityPage/ActivityPage';
import NavBar from '../NavBar/NavBar';
import './ActivityListPage.css';

class ActivityListPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      activities: [
        {
          activityName: 'Take a selfie',
          type: 'photo',
          activityDescription: 'You need to take a selfie to prove you have been here',
          isDone: false,
          activityID: 0
        },
        {
          activityName: 'Answer a question',
          type: 'question',
          activityDescription: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua',
          isDone: false,
          activityID: 1,
          options: [
            {text: 'Option 1', correct: false},
            {text: 'Option 2', correct: true},
            {text: 'Option 3', correct: false},
            {text: 'Option 4', correct: false}
          ]
        }
      ],
    }

    this.renderRow = this.renderRow.bind(this);
    this.setDone = this.setDone.bind(this);
    this.renderThumbnail = this.renderThumbnail.bind(this);
    this.pushPage = this.pushPage.bind(this);
  }

  componentWillUnmount() {
    this.props.resetState();
  }

  renderThumbnail(activityType) {
    if (activityType === 'photo') {
      return <Icon icon='camera' size={32} className='list-item__thumbnail' style={{color: '#FABC39', height: 'auto', width: 10}} />
    } else if (activityType === 'question') {
      return <Icon icon='question-circle' size={32} className='list-item__thumbnail' style={{color: '#AB19DB', height: 'auto', width: 10}} />
    }
  }

  setDone(id) {
    const newActivities = this.state.activities.map((row) => {
      if (row.activityID === id) row.isDone = true;
      return row;
    });

    console.log(newActivities);
    
    this.setState({
      activities: newActivities
    });
  }

  pushPage(id) {
    this.props.navigator.pushPage({
      component: ActivityPage,
      props: {
        activities: this.state.activities,
        id: id,
        setDone: this.setDone,
        result: this.props.result
      }
    });
  }

  renderRow(row, index) {
    return (
      <ListItem
        key={row.activityID}
        modifier={index === this.state.activities.length - 1 ? 'longdivider' : null}
        onClick={() => {
          this.pushPage(row.activityID);
        }}
        tappable
      >
        <div className='left ActivityListPage-thumbnail' style={{minWidth: 60}}>
        {/* <img src={row.image} className='list-item__thumbnail LocationsPage-thumbnail' /> */}
          {this.renderThumbnail(row.type)}
          { row.isDone && <Icon icon='check-circle' size={24} className='ActivityListPage-checkmark' /> }
        </div>
        <div className='center ActivityListPage-task_title'>
            {row.activityName}
        </div>
        <div className='right'>
          <Icon icon='fa-chevron-right' style={{ color: '#CCC' }} />
        </div>
      </ListItem>
    );
  }

  render() {
    return (
      <Page renderToolbar={() => 
        <NavBar title='Activities' navigator={this.props.navigator} icon={<Icon icon='tasks' />} backButton />
      }>
        <List
          dataSource={this.state.activities}
          renderRow={this.renderRow}
          renderHeader={() => <ListHeader style={{textAlign: 'center'}}>{this.props.location}</ListHeader>} 
        />
      </Page>
    );
  }
}

export default ActivityListPage;