import React, { Component } from 'react';
import * as ons from 'onsenui';
import { Page, Button, Icon } from 'react-onsenui';
import QrReader from 'react-qr-reader';
import ActivityListPage from '../ActivityListPage/ActivityListPage';
import NavBar from '../NavBar/NavBar';
import './LocationPage.css';

class LocationPage extends Component {
  constructor(props) {
    super(props);
    console.log(this.props);

    this.state = {
      result: null,
      displayCamera: false
    }

    this.getMap = this.getMap.bind(this);
    this.handleScan = this.handleScan.bind(this);
    this.handleError = this.handleError.bind(this);
    this.displayCamera = this.displayCamera.bind(this);
    this.hideCamera = this.hideCamera.bind(this);
    this.pushPage = this.pushPage.bind(this);
    this.resetState = this.resetState.bind(this);
    this.renderDescription = this.renderDescription.bind(this);
    this.renderCamera = this.renderCamera.bind(this);
  }

  handleScan = data => {
    const location = this.props.locations[this.props.id];
    if (data) {
      console.log(data);
      
      if (data === location.title) {
        this.setState({
          result: data
        });
        this.pushPage(this.props.navigator);
      } else {
        this.setState({
          displayCamera: false
        });
        ons.notification.alert(`Sorry, looks like you are not in ${location.title}`);
      }
    }
  }

  handleError = err => {
    console.error(err)
  }

  displayCamera() {
    this.setState({
      displayCamera: true
    });
  }

  hideCamera() {
    this.setState({
      displayCamera: false
    });
  }

  getMap(locationName) {
    window.open(`https://maps.google.com/?q=${locationName.replace(' ', '+')}`);
  }

  resetState() {
    this.setState({
      result: null,
      displayCamera: false
    });
  }

  pushPage(navigator) {
    navigator.pushPage({
      component: ActivityListPage,
      props: {
        result: this.state.result,
        resetState: this.resetState,
        location: this.props.locations[this.props.id].title
      },
    });
  }

  renderDescription() {
    console.log(this.props);
    const location = this.props.locations[this.props.id];
    console.log("Location: " + location);
    return (
      <div>
        <div style={{ textAlign: 'center' }}>
          <img src={location.image} alt={location.title} style={{ width: '100%' }} />
        </div>
        <div className="title center" style={{ textAlign: 'center', fontFamily: 'Roboto', fontWeight: 700, fontSize: 22, color: '#777', textTransform: 'uppercase', marginTop: 15 }}>
          {location.title}
        </div>
        <div className="content" style={{ fontSize: 17, fontFamily: 'Open Sans', padding: 14, color: '#777', lineHeight: 1.2, textAlign: "left", padding: '0 6%'}}>
          <div style={{ display: 'flex', justifyContent: 'space-between', flexWrap: 'wrap' }}>
            <div>
              <p style={{ marginBottom: 0, color: '#42bdff', fontFamily: 'Roboto', fontWeight: 500 }}>Address</p>
              <p style={{ marginTop: 2, fontSize: 14, fontFamily: 'Roboto' }}>{location.address}</p>
            </div>
            <div style={{ textAlign: 'right' }}>
              <p style={{ marginBottom: 0, color: '#42bdff', fontFamily: 'Roboto', fontWeight: 500 }}>Hours</p>
              <p style={{ marginTop: 2, fontSize: 14, fontFamily: 'Roboto' }}>{location.hours}</p>
            </div>
          </div>
          <p style={{ marginBottom: 0, marginTop: 0, color: '#42bdff', fontFamily: 'Roboto', fontWeight: 500 }}>Description</p>
          {location.description}
        </div>
        <section style={{ margin: '16px' }}>
          <div style={{ margin: '6px' }}>
            <Button onClick={this.displayCamera} modifier='large'>I am here</Button>
          </div>
          <div style={{ margin: '6px' }}>
            <Button onClick={() => this.getMap(location.title)} modifier='large outline'>Get directions</Button>
          </div>
        </section>
      </div>
    );
  }

  renderCamera() {
    return (
      <div>
        <div className='LocationPage-QR_description'>
          <p><Icon icon='qrcode' size={68} /></p>
          <p>Find QR code and scan it</p>
        </div>
        <QrReader
          delay={1000}
          onError={this.handleError}
          onScan={this.handleScan}
          style={{ width: '100%' }}
        />
        <Button onClick={this.hideCamera} modifier='large' className='LocationPage-QR_cancel_btn'>Cancel</Button>
      </div>
    );
  }

  render() { 
    return (
      <Page renderToolbar={() => 
        <NavBar
          title={this.props.locations[this.props.id].title}
          navigator={this.props.navigator}
          backButton
        />
      }>
        {this.state.displayCamera ? this.renderCamera() : this.renderDescription()}
      </Page>
    );
  }
}

export default LocationPage;