import React from 'react';
import { Toolbar, BackButton } from 'react-onsenui';
import './NavBar.css';

const NavBar = ({title, navigator, icon, backButton}) => (
  <Toolbar className='NavBar'>
    <div className='left NavBar-left'>
      {backButton ? <BackButton onClick={() => navigator.popPage()}></BackButton> : null}
    </div>
    <div className='center NavBar-center'>
      {icon} {title}
    </div>
  </Toolbar>
);

export default NavBar;