import React, { Component } from 'react';
import { Page, List, ListItem, Icon } from 'react-onsenui';
import './LocationsPage.css';
import NavBar from '../NavBar/NavBar';
import LocationPage from '../LocationPage/LocationPage';

class LocationsPage extends Component {
  constructor(props) {
    super(props);

    this.handleLocationSelection = this.handleLocationSelection.bind(this);
    this.renderRow = this.renderRow.bind(this);
  }

  handleLocationSelection(id) {
    console.log("id" + id);
    this.props.navigator.pushPage({
      component: LocationPage,
      key: 'LOCATION_PAGE',
      props: {
        locations: this.props.locations,
        id: id
      }
    });
  }

  renderRow(row, index) {
    return (
      <ListItem
        key={row.objectID}
        modifier={index === this.props.locations.length - 1 ? 'longdivider' : null}
        onClick={() => {
          this.handleLocationSelection(row.objectID);
        }}
        tappable
      >
        <div className='left' style={{minWidth: 60}}>
          <img src={row.image} className='list-item__thumbnail LocationsPage-thumbnail' />
          { row.completed && <Icon icon='check-circle' size={24} className='LocationsPage-checkmark' /> }
        </div>
        <div className='center'>
          <div className='list-item__title'>
            {row.title}
          </div>
          <div className='list-item__subtitle'>
            {row.subtitle}
          </div>
        </div>
        <div className='right'>
          <Icon icon='fa-chevron-right' style={{ color: '#CCC' }} />
        </div>
      </ListItem>
    );
  }

  render() {
    return (
      <Page renderToolbar={() => 
        <NavBar title='Locations' navigator={this.props.navigator} icon={<Icon icon='map-marker-alt' />} />
      }>
        <List
          dataSource={this.props.locations}
          renderRow={this.renderRow}
        />
      </Page>
    );
  }
}

export default LocationsPage;