import React, { Component } from 'react';
import * as ons from 'onsenui';
import { Page, Input, Button } from 'react-onsenui';
import LocationsPage from '../LocationsPage/LocationsPage';
import './LoginPage.css';
import SignUpPage from '../SignUpPage/SignUpPage';

class LoginPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      email: '',
      password: ''
    };

    this.handleEmailChange = this.handleEmailChange.bind(this);
    this.handlePasswordChange = this.handlePasswordChange.bind(this);
    this.handleLogin = this.handleLogin.bind(this);
    this.openSignUpPage = this.openSignUpPage.bind(this);
  }

  handleEmailChange(e) {
    this.setState({ email: e.target.value });
  }

  handlePasswordChange(e) {
    this.setState({ password: e.target.value });
  }

  handleLogin() {
    if (this.state.email === '1' && this.state.password === '2') {
      this.props.navigator.pushPage({
        component: LocationsPage,
        key: 'LOCATIONS_PAGE',
        props: {
          locations: this.props.locations,
        }
      });
    } else {
      ons.notification.alert({message: 'Username or password is incorrect', title: 'Ooooops!'});
    }
  }

  openSignUpPage() {
    this.props.navigator.pushPage({
      component: SignUpPage,
      props: {
        locations: this.props.locations
      }
    })
  }

  render() {
    return (
      <Page>
        <div className='LoginPage'>
          <img src='logo-white.png' width='200' />
          <section className='LoginPage-form'>
            <p className='LoginPage-title'>Scavenger Hunt</p>
            <Input
              value={this.state.email}
              onChange={this.handleEmailChange}
              type='email'
              disable-auto-styling
              placeholder='Email'
              className='LoginPage-input' />
            <Input
              value={this.state.password}
              onChange={this.handlePasswordChange}
              type='password'
              disable-auto-styling
              placeholder='Password'
              className='LoginPage-input' />
            <Button modifier='large' className='LoginPage-login_btn' onClick={this.handleLogin}>SIGN IN</Button>
            <Button modifier='large--quiet' className='LoginPage-signup_btn' onClick={this.openSignUpPage}>or create new account</Button>
          </section>
          <img src='team-logo-white.png' width='100' />
        </div>
      </Page>
    );
  }
}

export default LoginPage;