import React, { Component } from 'react';
import { Page, Icon, List, ListItem, Radio } from 'react-onsenui';
import NavBar from '../NavBar/NavBar';
import './ActivityPage.css';

class ActivityPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedAnswer: null,
    }

    this.renderPhotoTask = this.renderPhotoTask.bind(this);
    this.renderQuestionTask = this.renderQuestionTask.bind(this);
    this.renderRadioRow = this.renderRadioRow.bind(this);
    this.handleOptionChange = this.handleOptionChange.bind(this);
  }

  componentWillUnmount() {
    this.props.setDone(this.props.id);
  }

  renderPhotoTask() {
    const activity = this.props.activities[this.props.id];

    return (
      <div style={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
        {activity.isDone &&
          <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', flexBasis: '15%', color: '#FFF', backgroundColor: '#21CC41', fontSize: 28, fontWeight: 300 }}>
            <Icon icon='check' size={28} style={{ color: '#FFF' }} />&nbsp;&nbsp;Completed!
        </div>}
        <div style={{ flexBasis: '50%', margin: '2rem 0', textAlign: 'center' }}>
          <span style={{ display: 'inline-block', padding: '0.4rem 0.6rem', color: '#FFF', backgroundColor: '#FABC39', borderRadius: '10%', width: '40%' }}>
            {activity.activityName}
          </span>
          <p style={{ fontSize: 20 }}>{activity.activityDescription}</p>
        </div>
        {!activity.isDone &&
          <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', flexBasis: '50%', backgroundColor: '#EEE', textAlign: 'center' }}>
            <label>
              <Icon icon='camera' size={72} style={{ color: '#FABC39' }} />
              <input style={{ display: 'none' }} type="file" accept="image/*;capture=camera" />
            </label>
          </div>
        }
      </div>

    );
  }

  handleOptionChange(text) {
    this.setState({ selectedAnswer: text });
  }

  renderRadioRow(row, index) {
    return (
      <ListItem key={index} tappable>
        <label className='left'>
          <Radio
            inputId={`radio-${row.text}`}
            checked={row.text === this.state.selectedAnswer}
            onChange={() => this.handleOptionChange(row.text)}
          />
        </label>
        <label htmlFor={`radio-${row.text}`} className='center' style={{fontSize: 22}}>
          {row.text}
        </label>
      </ListItem>
    );
  }

  renderQuestionTask() {
    const activity = this.props.activities[this.props.id];

    return (
      <div style={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
        {activity.isDone &&
          <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', flexBasis: '15%', color: '#FFF', backgroundColor: '#21CC41', fontSize: 28, fontWeight: 300 }}>
            <Icon icon='check' size={28} style={{ color: '#FFF' }} />&nbsp;&nbsp;Completed!
          </div>}
        <div style={{ flexBasis: '30%', margin: '2rem 0', textAlign: 'center' }}>
          <span style={{ display: 'inline-block', padding: '0.4rem 0.6rem', color: '#FFF', backgroundColor: '#AB19DB', borderRadius: '10%', width: '40%' }}>
            {activity.activityName}
          </span>
          <p style={{ fontSize: 20, paddingTop: 30 }}>{activity.activityDescription}</p>
        </div>
        <List
          dataSource={activity.options}
          renderRow={this.renderRadioRow}
        />
      </div>
    );
  }

  render() {
    const activity = this.props.activities[this.props.id];

    return (
      <Page renderToolbar={() =>
        <NavBar
          title='Task'
          navigator={this.props.navigator} icon={<Icon icon='thumbtack' />}
          backButton />
      }>
        {activity.type === 'photo' ? this.renderPhotoTask() : this.renderQuestionTask()}
      </Page>
    );
  }
}

export default ActivityPage;