import React, { Component } from 'react';
import * as ons from 'onsenui';
import { Page, Input, Button } from 'react-onsenui';
import './SignUpPage.css';

class SignUpPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      username: '',
      email: '',
      password1: '',
      password2: ''
    };

    this.handleUsernameChange = this.handleUsernameChange.bind(this);
    this.handleEmailChange = this.handleEmailChange.bind(this);
    this.handlePassword1Change = this.handlePassword1Change.bind(this);
    this.handlePassword2Change = this.handlePassword2Change.bind(this);
    this.handleRegistration = this.handleRegistration.bind(this);
  }

  handleUsernameChange(e) {
    this.setState({ username: e.target.value });
  }

  handleEmailChange(e) {
    this.setState({ email: e.target.value });
  }

  handlePassword1Change(e) {
    this.setState({ password1: e.target.value });
  }

  handlePassword2Change(e) {
    this.setState({ password2: e.target.value });
  }

  handleRegistration() {
    ons.notification.alert({message: 'Not implemented yet', title: 'Ooooops!'});
  }

  openSignUpPage() {
    this.props.navigator.pushPage({
      component: SignUpPage,
      props: {
        locations: this.props.locations
      }
    })
  }

  render() {
    return (
      <Page>
        <div className='LoginPage'>
          <img src='logo-white.png' width='200' />
          <section className='LoginPage-form'>
            <p className='LoginPage-title'>Sign Up</p>
            <Input
              value={this.state.username}
              onChange={this.handleUsernameChange}
              type='text'
              disable-auto-styling
              placeholder='Name'
              className='LoginPage-input' />
            <Input
              value={this.state.email}
              onChange={this.handleEmailChange}
              type='email'
              disable-auto-styling
              placeholder='Email'
              className='LoginPage-input' />
            <Input
              value={this.state.password1}
              onChange={this.handlePassword1Change}
              type='password'
              disable-auto-styling
              placeholder='Password'
              className='LoginPage-input' />
            <Input
              value={this.state.password2}
              onChange={this.handlePassword2Change}
              type='password'
              disable-auto-styling
              placeholder='Repeat password'
              className='LoginPage-input' />
            <Button modifier='large' className='SignUpPage-register_btn' onClick={this.handleRegistration}>CREATE ACCOUNT</Button>
            <Button modifier='large--quiet' className='LoginPage-signup_btn' onClick={() => this.props.navigator.popPage()}>I've got an account</Button>
          </section>
          <img src='team-logo-white.png' width='100' />
        </div>
      </Page>
    );
  }
}

export default SignUpPage;